@import url(https://fonts.googleapis.com/css?family=Merriweather+Sans&display=swap);
html, body {
  margin: 0;
  padding: 0; }

:root {
  --color: #fff;
  --size: 50px;
  --width: 50px;
  --speed: 2s; }

body {
  font-size: 62.5%;
  font-family: 'Merriweather Sans', sans-serif; }

.app {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 3rem; }
  @media only screen and (max-width: 93.75em) {
    .app {
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 1.2rem;
      row-gap: 1.2rem; } }
  @media only screen and (max-width: 56.25em) {
    .app {
      grid-template-columns: minmax(-webkit-min-content, 1fr);
      grid-template-columns: minmax(min-content, 1fr); } }
  @media only screen and (max-width: 56.25em) {
    .app {
      padding: 1rem; } }
  @media only screen and (max-width: 30em) {
    .app {
      padding: 1rem; } }
  .app > * {
    padding: 2rem;
    border-radius: 8px; }
    @media only screen and (max-width: 56.25em) {
      .app > * {
        padding: 1.2rem; } }
  @media only screen and (max-width: 93.75em) {
    .app > *:last-of-type {
      grid-area: 2/1/3/3; } }
  @media only screen and (max-width: 56.25em) {
    .app > *:last-of-type {
      grid-area: auto; } }

.controlpannel {
  background-image: -webkit-gradient(linear, left top, right bottom, from(#2a2a25), to(#4e4e47));
  background-image: linear-gradient(to right bottom, #2a2a25, #4e4e47);
  color: white;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 1.2rem;
  row-gap: 1.2rem;
  font-size: 1.2rem;
  box-shadow: 0.4rem 0.4rem 0.5rem #2a2a25; }
  .controlpannel__title {
    font-size: 1.6rem; }
  .controlpannel > *:last-child {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center; }

input[type=range] {
  background-color: transparent;
  height: 38px;
  -webkit-appearance: none;
  width: 100%; }

input[type=range]:focus {
  outline: none; }

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  background: -webkit-gradient(linear, left top, right top, from(white), to(#fff));
  background: linear-gradient(to right, white, #fff);
  background: -webkit-gradient(linear, left top, right top, from(white), to(var(--color)));
  background: linear-gradient(to right, white, var(--color));
  border-radius: 5px;
  border: 1px solid #2a2a25; }

input[type=range]::-webkit-slider-thumb {
  border: 1px solid white;
  height: 30px;
  width: 15px;
  border-radius: 5px;
  background: #fff;
  background: var(--color);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -11px; }

input[type=color] {
  -webkit-appearance: none;
  border: none;
  width: 32px;
  height: 32px;
  outline: none;
  border-radius: 50%; }

input[type=range]:focus {
  outline: none; }

input[type=color]::-webkit-color-swatch-wrapper {
  padding: 0;
  outline: none;
  border-radius: 50%; }

input[type=color]::-webkit-color-swatch {
  border: none;
  outline: none;
  border-radius: 50%;
  cursor: pointer; }

.code {
  background-image: -webkit-gradient(linear, right top, left top, from(#4e4e47), to(#2a2a25));
  background-image: linear-gradient(to left, #4e4e47, #2a2a25);
  color: #f57647;
  box-shadow: 0.4rem 0.4rem 0.5rem #2a2a25;
  font-size: 1.2rem; }
  @media only screen and (max-width: 30em) {
    .code {
      font-size: 0.8rem; } }
  .code__title {
    color: white;
    font-size: 1.2rem; }
  .code__copy {
    font-size: 0.8rem;
    padding: 1px 5px;
    border-radius: 3px;
    color: #2a2a25;
    background-color: white; }
    .code__copy:hover {
      -webkit-transition: all .3s linear;
      transition: all .3s linear;
      cursor: pointer;
      box-shadow: 0.4rem 0.4rem 0.5rem #2a2a25; }

.loader-wrapper {
  min-width: 29em; }

.loader {
  width: 50px;
  width: var(--size);
  height: 50px;
  height: var(--size);
  margin: 0 auto;
  border: 50px solid transparent;
  border: var(--width) solid transparent;
  border-top: 50px solid #fff;
  border-top: var(--width) solid var(--color);
  border-radius: 50%;
  -webkit-animation: rotate 2s infinite linear;
          animation: rotate 2s infinite linear;
  -webkit-animation: rotate var(--speed) infinite linear;
          animation: rotate var(--speed) infinite linear;
  box-sizing: border-box; }

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

